import React, { useEffect, useState } from 'react';
import Overview from './Overview';
import './City.css'
import { setUserData } from '../store/dataSlice'
import { useSelector, useDispatch } from 'react-redux';
import { selectUserData } from '../store/dataSlice';

const City = () => {
    const [city, setCity] = useState('Asia/Bangkok');
    const [capital, setCapital] = useState('Hanoi');
    const [value, setValue] = useState('Hanoi');
    const [bigData, setBigData] = useState({});
    const [dataInfo, setDataInfo] = useState({});
    const dispatch = useDispatch();
    const [err, setErr] = useState(false);
    const userData = useSelector(selectUserData);
    const [count, setCount] = useState(0);

    useEffect(() => {
        async function fetchWeatherInformation() {
            try {
                const apiKey = process.env.REACT_APP_WEATHER_API_KEY;
                const cityApi = `https://api.weatherapi.com/v1/forecast.json?key=${apiKey}&q=${capital}&days=10&aqi=no&alerts=yes`;
    
                const responses = await fetch(cityApi);
              
                if (!responses.ok) {
                    throw new Error(`HTTP error! status: ${responses.status}`);
                }

                const data = await responses.json();
                setErr(false);
                setBigData(data);
    
            } catch (error) {
                setErr(true);
                console.error("Error:", error);
            }
        }

        fetchWeatherInformation();
    }, [capital]);

    useEffect(() => {
        if (userData.active) {
            console.log('look at me' + userData.active);
        }
    }, [userData.active]);

    useEffect(() => {
        if (bigData && bigData.location) {
            setCity(bigData.location.tz_id);
            if (userData.active === 1) {
                setDataInfo((preDataInfo) => ({
                    ...preDataInfo,
                    temp_c : bigData.current.temp_c,
                    temp_f : bigData.current.temp_f,
                    text : bigData.current.condition.text,
                    icon : bigData.current.condition.icon,
                    humidity : bigData.current.humidity,
                    wind : bigData.current.wind_kph,
                }));
            }

            if (userData.active === 2) {
                setDataInfo((preDataInfo) => ({
                    ...preDataInfo,
                    temp_c: bigData.forecast.forecastday[1].day.avgtemp_c,
                    temp_f: bigData.forecast.forecastday[1].day.avgtemp_f,
                    text: bigData.forecast.forecastday[1].day.condition.text,
                    icon: bigData.forecast.forecastday[1].day.condition.icon,
                    humidity: bigData.forecast.forecastday[1].day.avghumidity,
                    wind: bigData.forecast.forecastday[1].day.maxwind_kph,
                }));
            }

            if (userData.active === 3) {
                setDataInfo((preDataInfo) => ({
                    ...preDataInfo,
                    temp_c: bigData.forecast.forecastday[2].day.avgtemp_c,
                    temp_f: bigData.forecast.forecastday[2].day.avgtemp_f,
                    text: bigData.forecast.forecastday[2].day.condition.text,
                    icon: bigData.forecast.forecastday[2].day.condition.icon,
                    humidity: bigData.forecast.forecastday[2].day.avghumidity,
                    wind: bigData.forecast.forecastday[2].day.maxwind_kph,
                }));
            }

            if ( count === 0 ) {
                const sendUserDataToStore = (today, nextDay, nextNextDay) => {
                    const userData = {
                    today: today,
                    tomorrow: nextDay,
                    overmorrow: nextNextDay,
                    active: 1,
                    };
                    dispatch(setUserData(userData));
                };

                sendUserDataToStore(bigData.forecast.forecastday[0], bigData.forecast.forecastday[1], bigData.forecast.forecastday[2]);
                setCount(1);
            }
        }

    }, [bigData, dispatch, userData.active]);

    useEffect(() => {

    }, [err])


    const inputCity = (event) => {
        if (event.key === 'Enter') {
            setCapital(value);
        }
    }

    const handleChange = (event) => {
        setValue(event.target.value);
    };

  return (
    <div>
        <div className='City'>
            <div className='City_time'>
                <p>Your city</p>
                <input type="text" value={value} onKeyDown={inputCity} onChange={handleChange}/>
            </div>
            {err && <span>Unavailable data</span>}
            { userData.active && <Overview city={city} dataInfo={dataInfo}/>}
        </div>
    </div>
  )
}

export default City