import React, { useState, useEffect } from 'react';
import moment from 'moment-timezone';
import './Overview.css'

const Overview = ({city, dataInfo}) => {
    const [time, setTime] = useState(moment().tz(city));
    const [degree, setDegree] = useState(true);

    useEffect(() => {
    const intervalId = setInterval(() => {
        setTime(moment().tz(city));
    }, 100);

    return () => clearInterval(intervalId);
    }, [city]);

    const toggleDegree = () => {
      setDegree((prevDegree) => !prevDegree);
    }


  return (
    <div className='Overview'>
        <div className='Overview_time'>
            <p>{time.format('h') + ':' + time.format('mm') + ' ' +  time.format('A') + ', ' + time.format('ddd') + ', ' + time.format('MMM') + ' ' + time.format('D') + ', ' + time.format('YYYY')}</p>
        </div>

        <div className='Overview_weather'>
          <div className='Overview_weather_status'>
            <img src={dataInfo.icon} alt=''/>
            <button onClick={toggleDegree}>{degree ? <p>{dataInfo.temp_c}<sup>&deg;C</sup></p> : <p>{dataInfo.temp_f}<sup>&deg;F</sup></p>}</button>
          </div>
          <p>{dataInfo.text}</p>
          <div className='Overview_weather_subStatus'>
            <div className='Overview_weather_subStatus_col'>
              <h6>Humidity</h6>
              <h5>{dataInfo.humidity}%</h5>
            </div>
            <div className='Overview_weather_subStatus_col'>
              <h6>Wind speed</h6>
              <h5>{dataInfo.wind} km/j</h5>
            </div>
          </div>

        </div>
    </div>
  )
}

export default Overview